import React from 'react';
import 'isomorphic-fetch';
import { Button, FormGroup, FormControl, ControlLabel, Form, Row, Col, Nav, NavItem, NavDropdown, MenuItem, ButtonToolbar, Tabs, Tab, TabContainer, TabContent, TabPane} from 'react-bootstrap';
import { SocketProvider } from 'socket.io-react';
import SocketIOClient from 'socket.io-client';
import TelnetDiagnostics from './TelnetDiagnostics.jsx';
let socket;

export default class Diagnostics extends React.Component {
 
constructor(props){
    	super(props);
	this.state = {
		text: '',
		host: '127.0.0.1',
		port: 5250,
		command: "cls",
		response: '',
	}
	this.onHostChange = this.onHostChange.bind(this);
	this.onPortChange = this.onPortChange.bind(this);
	this.onCommandChange = this.onCommandChange.bind(this);
	this.sendTelnetTest = this.sendTelnetTest.bind(this);
}
componentDidMount() {
	socket = SocketIOClient();
	socket.on(this.props.location.pathname, (mesg) => {
		this.setState({text: mesg});
	});
	socket.on('telnet-response', (mesg) => {
		this.setState({response: mesg});
	});
}
componentWillUnmount() {
	socket.off(this.props.page);
}
onPortChange(event) {
    this.setState({ port: event.target.value });
 }
 onHostChange(event) {
    this.setState({ host: event.target.value });
 }
 onCommandChange(event) {
    this.setState({ command: event.target.value });
 }
sendTelnetTest() {
  console.log("sending Telnet Test");
  socket.emit('diagnostics-send-telnet', { host: this.state.host, port: this.state.port, command: this.state.command});
}
 render() {
 	return (
	<div>
		<Tabs defaultActiveKey={2} id="uncontrolled-tabs">
		  <Tab eventKey={"telnet-tab"} title="Telnet">
		    <TelnetDiagnostics />
		  </Tab>
		  <Tab eventKey={"dmx-tab"} title="DMX">
		   
		  </Tab>
		  <Tab eventKey={"midi-tab"} title="MIDI">
		    
		  </Tab>
		  <Tab eventKey={"osc-tab"} title="OSC">
		    
		  </Tab>
		</Tabs>
	</div>
    );
  };
}




